.profile-page-container {
    width: 100%;
    padding: 8rem 6%;
    display: flex;
    justify-content: center;
    gap: 2rem;
}

.user-widget-profile {
    flex-basis: 26%;
}

.friend-list-widget-profile,
.posts-widget-profile {
    margin: 2rem 0;
}

.my-post-widget-profile {
    flex-basis: 42%;
    margin-top: 0;
}

@media (max-width: 1000px) {
    .profile-page-container {
        display: block;
    }
}
